@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
    padding: 0;
    margin: 0;
    font-family: "Roboto", sans-serif;
    color: #e9e9eb;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}

.container {
    background-color: #13171d;
}

.flex-wrapper {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    justify-content: space-between;
}

.grid-wrapper {
    display: grid;
    min-height: 100vh;
    justify-content: space-between;
    grid-template-columns: 1fr;
    grid-template-rows: 70px auto 50px;
}

.nav-wrapper {
    background-color: #2b2d42;
    max-height: 70px;
}

.navbar {
    height: 70px;
    padding: 0px 100px;
    margin: 0px 200px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;
}

.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-item:first-child {
    grid-column: 1/6;
}

.logo {
    font-size: 40px;
    font-weight: 700;
}

ul {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, minmax(130px, 100px));
    place-content: center;
    place-items: end;
    list-style: none;
}

li {
    text-decoration: none;
}

.link {
    text-decoration: none;
    color: #bcbedc;
}

li > .link {
    font-size: 18px;
    font-weight: 500;
}

li > .link:hover {
    color: #e9e9eb;
}

.get-started-btn {
    padding: 10px 15px;
    font-size: 20px;
    border: 1px solid #7959ec;
    border-radius: 4px;
}

.get-started-btn:hover {
    border: 1px solid transparent;
    background-color: #7959ec;
    cursor: pointer;
}

.get-started-btn > .link:hover {
    color: #e9e9eb;
}

.navbar-user-container {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.navbar-user-image {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: none;
}

.navbar-user-options {
    margin-left: 5px;
}

.card {
    display: flex;
    flex-direction: column;
}

.card-image {
    width: 185px;
    height: 265px;
    margin-bottom: 5px;
    object-fit: cover;
    border-radius: 5px;
    cursor: pointer;
}

.card-image:hover {
    box-shadow: #313644 0px 0px 29px 0px;
}

.card-title {
    font-size: 15px;
    font-weight: 500;
    text-decoration: none;
    color: #bcbedc;
}

.card-title:hover {
    color: #e9e9eb;
}

.list {
    display: grid;
    grid-template-columns: repeat(auto-fill, 185px);
    grid-template-rows: 317px;
    grid-gap: 28px 30px;
    gap: 28px 30px;
    justify-content: space-between;
}

.searchbar {
    width: 380px;
    height: 30px;
    font-size: 18px;
    padding: 5px 15px;
    border-radius: 5px;
    border: none;
    color: #7975ec;
}

.home-container {
    width: 100vw;
    overflow: scroll;
    /* position: fixed; */
    /* background-color: #13171d;
    z-index: 10; */
}

.home-wrapper {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 0px 100px;
    margin: 30px 200px 20px;
}

.landing-container {
    background-image: url("https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2670&q=80");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 4px;
    width: 100%;
    height: 830px;
    margin: 30px 0px 50px;
}

.landing-container > div {
    height: 100%;
    padding: 260px 50px 20px;
}

.landing-title {
    font-size: 74px;
    font-weight: 700;
    color: #7975ec;
}

.landing-small-title {
    font-size: 40px;
    font-weight: 700;
}

.landing-btn {
    padding: 10px 15px;
    border-radius: 4px;
    font-size: 24px;
    margin-top: 15px;
}

.landing-toBrowse-btn {
    background-color: #7975ec;
    color: #e9e9eb;
    border: none;
}

.landing-getStarted-btn {
    background-color: transparent;
    color: #7975ec;
    border: 1px solid #7975ec;
    margin-right: 15px;
}

.landing-getStarted-btn:hover {
    background-color: #7975ec;
    color: #e9e9eb;
}

.form-container > form {
    max-width: 500px;
    padding: 50px;
    margin: 0 auto;
    border-radius: 10px;
}
.form-name {
    font-size: 40px;
    border-bottom: 1px solid #e9e9eb;
    padding-bottom: 20px;
    text-transform: uppercase;
}
.form > p {
    color: tomato;
}

form > p::before {
    display: inline;
    content: "⚠ ";
    color: tomato;
}

.input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    border-radius: 4px;
    border: 1px solid #e9e9eb;
    padding: 10px 15px;
    margin-bottom: 10px;
    font-size: 14px;
    color: #0e101c;
}

label {
    line-height: 2;
    text-align: left;
    display: block;
    margin-bottom: 13px;
    margin-top: 20px;
    color: #e9e9eb;
    font-size: 16px;
    font-weight: 200;
}

.form_container > button[type="submit"] {
    background: #7959ec;
    color: #e9e9eb;
    text-transform: uppercase;
    border: none;
    margin-top: 40px;
    padding: 20px;
    font-size: 16px;
    font-weight: 100;
}
input[type="submit"] {
    background: #7959ec;
    color: #e9e9eb;
    text-transform: uppercase;
    border: none;
    margin-top: 20px;
    padding: 20px;
    font-size: 16px;
    font-weight: 100;
    cursor: pointer;
}

.form_container > button[type="submit"]:hover,
input[type="submit"]:hover {
    background: tomato;
}

.form_container > button[type="submit"]:active,
input[type="button"]:active,
input[type="submit"]:active {
    transition: 0.3s all;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    /* border: 1px solid transparent; */
    opacity: 0.8;
}

input:disabled {
    opacity: 0.4;
}

input[type="button"]:hover {
    transition: 0.3s all;
}

.form_container > button[type="submit"],
input[type="button"],
input[type="submit"] {
    -webkit-appearance: none;
}

.App {
    max-width: 600px;
    margin: 0 auto;
}

button[type="button"] {
    display: block;
    -webkit-appearance: none;
            appearance: none;
    background: #333;
    color: #e9e9eb;
    border: none;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 4px;
}

hr {
    margin-top: 30px;
}

.form_container > form > button {
    display: block;
    -webkit-appearance: none;
            appearance: none;
    margin-top: 40px;
    border: 1px solid #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 4px;
}

.form-container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 80px;
}

.comment-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, auto));
    grid-template-rows: repeat(2, 1fr);
    align-items: center;
    border: 1px solid white;
    border-radius: 4px;
    padding: 20px;
    margin-bottom: 15px;
}

.comment-user {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.comment-item:last-child {
    grid-column: 1 / auto;
}

.wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, auto));
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 15px;
}

.wrapper-item:first-child {
    grid-column: span 6 / auto;
}

.wrapper-item:last-child {
    grid-column: span 6 / auto;
}

.comments-input {
    height: 50px;
    padding: 0px 20px;
    font-size: 20px;
    color: #13171d;
}

.comments-submit-btn {
    height: 50px;
    width: 100%;
    background-color: #7959ec;
}

.comments-submit-btn,
.comments-input {
    border: none;
    border-radius: 4px;
}

.detail-container {
    margin: 40px 200px 40px;
    padding: 0px 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.detail-back-button {
    text-decoration: none;
    margin-bottom: 15px;
    background-color: #7959ec;
    border: none;
    border-radius: 4px;
    width: 160px;
    padding: 10px 20px;
    display: flex;
    flex-flow: row;
    justify-content: flex-start;
    align-items: center;
}

.detail-back-button > h3 {
    margin: auto;
}

.detail-image {
    width: 200px;
    height: 265px;
    border: none;
    border-radius: 4px;
    object-fit: cover;
    box-shadow: #313644 0px 0px 29px 0px;
}

.detail-title {
    font-size: 40px;
    font-weight: 700;
}

.detail-year,
.detail-country {
    font-size: 25px;
}

.detail-title,
.detail-genres {
    margin-left: 50px;
    width: 100%;
}

.detail-button-container {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    width: 100%;
}

.rate-btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: 1px solid #7959ec;
    color: #7959ec;
    font-size: 20px;
    width: 70%;
}

.rate-btn:hover {
    background-color: #7959ec;
    color: #e9e9eb;
}

.like-btn {
    background-color: tomato;
    border: none;
    font-size: 20px;
    width: 25%;
}

.rate-btn,
.like-btn {
    padding: 10px;
    height: 100%;
    border-radius: 4px;
    cursor: pointer;
}

.detail-genres {
    margin-top: 10px;
    display: grid;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(30px, auto));
}

.detail-genres > h3 {
    font-size: 30px;
}

.detail-genre {
    background-color: tomato;
    color: #e9e9eb;
    padding: 5px 10px;
    border-radius: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 18px;
    text-transform: uppercase;
}

.detail-comments {
    padding: 20px;
}

.detail-statics {
    padding: 20px;
    background-color: #2b2d42;
    border-radius: 4px;
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
}

.detail-content-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 25px 20px;
    gap: 25px 20px;
    margin-top: 30px;
}

.detail-content-item:first-child,
.detail-content-item:last-child {
    grid-column: span 5 / auto;
}

.detail-content-item {
    background-color: #2b2d42;
    border-radius: 4px;
    padding: 20px;
}

.detail-rating {
    width: 150;
    height: 150;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.detail-rating > h3 {
    padding-bottom: 10px;
}

.bottom-line {
    border-bottom: 1px solid white;
    padding: 5px 0px;
}

.detail-description {
    font-size: 20px;
}

button {
    cursor: pointer;
}

.admin-container {
  height: 80vh;
  padding: 1rem 1rem;
  display: flex;
}

.left {
  width: 20%;
  margin-right: 2rem;
}

.right {
  width: 75%;
  padding-top: 1rem;
  overflow-y: visible;
  overflow-x: hidden;
}

.loading {
  width: 100%;
  height: 80%;
  text-align: center;
}

.admin-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-left: 1rem;
}

.admin-nav-item {
  font-size: 1.5rem;
  border: 1px solid #7959ec;
  border-radius: 4px;
  padding: 0.5rem;
  cursor: pointer;
  transition: scale ease 0.75s;
}

.admin-nav-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #7959ec;
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.admin-overview-item {
  border: 1px solid #7959ec;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
  width: 30%;
  border-radius: 6%;
}

.item-title {
  font-size: 1.5rem;
}

.item-detail {
  font-size: 2.5rem;
}

.movies-table {
  width: 100%;
  border-collapse: collapse;
}

td,
th {
  border: 1px solid #7959ec;
  text-align: left;
  padding: 1rem 0.5rem;
}

.function {
  cursor: pointer;
}



.footer-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0px 100px;
    margin: 0px 200px;
    color: #bcbedc;
    font-size: 14px;

    bottom: 0;
}

.social-links {
    display: grid;
    grid-template-columns: repeat(4, minmax(50px, 1fr));
}

.footer {
    bottom: 0;
    background-color: #0b1622;
    height: 120px;
    max-height: 120px;
    min-width: 100vw;
}

